.signup-center {
  position: relative;
  text-align: center;
  justify-content: center;
  float: center;
}

/*only in case of bottom bar*/
.signup-buffer {
  margin-bottom: 15%;
}

.titles {
  position: relative;
  width: 75%;
  margin: auto;
  text-align: center;
  justify-content: center;
}
.titles h1 {
  font-weight: 700;
  font-size: 3.5rem;
  letter-spacing: -0.1rem;
}
.titles h2 {
  font-weight: 700;
  font-size: 2rem;
  letter-spacing: -0.1rem;
  color: #0c0847;
  margin-top: 5%;
}

.subtitles {
  font-weight: 700;
  color: #0c0847;
  font-size: 1.5rem;
  margin-top: 5%;
}

.signup-btn {
  width: 30%;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  font-size: 1rem;
  border-radius: 8px;

  background-color: #afba15;
  border-color: #02001a;
  color: #0c0847;
}
.signup-btn:hover {
  background-color: #02001a;
  border-color: white;
  color: white;
}
.signup-btn:focus {
  background-color: #02001a;
  border-color: white;
}
.signup-btn:active {
  background-color: #0c0847;
  border-color: white;
}

.signup-confirm {
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  font-size: 1rem;
  border-radius: 8px;

  background-color: #0c0847;
  border-color: #02001a;
  color: white;
}
.signup-confirm:hover {
  background-color: #02001a;
  border-color: white;
  color: white;
}
.signup-confirm:focus {
  background-color: #02001a;
  border-color: white;
}
.signup-confirm:active {
  background-color: #0c0847;
  border-color: white;
}

.signup-skip {
  width: 60%;
  float: center;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  font-size: 1rem;
  border-radius: 8px;

  background-color: white;
  border-color: #02001a;
  color: #0c0847;
}
.signup-skip:hover {
  border-color: white;
}
.signup-skip:focus {
  border-color: white;
}
.signup-skip:active {
  border-color: white;
}

.signup-dropdown {
  border-radius: 0.25rem;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: lightgray;
}
.signup-dropdown:hover {
  background-color: gray;
}

.auth-error {
  color: red;
  font-size: 20px;
  float: center;
  text-align: center;
  justify-content: center;
  font-weight: 600;
  margin: auto;
}

.success {
  color: #afba15;
  font-size: 20px;
  float: center;
  text-align: center;
  justify-content: center;
  font-weight: 600;
  margin: auto;
}
